html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

.icon-facebook {
  width: 0.53em;
  height: 1em;
  fill: #000;
}

.icon-next {
  width: 1em;
  height: 1em;
  fill: initial;
}

.icon-prev {
  width: 1em;
  height: 1em;
  fill: initial;
}

/* Slider */
.slick-slider {
  position: relative;
  display: block;
  box-sizing: border-box;
  -moz-box-sizing: border-box;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -ms-touch-action: pan-y;
  touch-action: pan-y;
  -webkit-tap-highlight-color: transparent;
}

.slick-list {
  position: relative;
  overflow: hidden;
  display: block;
  margin: 0;
  padding: 0;
}

.slick-list:focus {
  outline: none;
}

.slick-list.dragging {
  cursor: pointer;
  cursor: hand;
}

.slick-slider .slick-track,
.slick-slider .slick-list {
  -webkit-transform: translate3d(0, 0, 0);
  -moz-transform: translate3d(0, 0, 0);
  -ms-transform: translate3d(0, 0, 0);
  -o-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
}

.slick-track {
  position: relative;
  left: 0;
  top: 0;
  display: block;
}

.slick-track:before, .slick-track:after {
  content: "";
  display: table;
}

.slick-track:after {
  clear: both;
}

.slick-loading .slick-track {
  visibility: hidden;
}

.slick-slide {
  float: left;
  height: 100%;
  min-height: 1px;
  display: none;
}

[dir="rtl"] .slick-slide {
  float: right;
}

.slick-slide img {
  display: block;
}

.slick-slide.slick-loading img {
  display: none;
}

.slick-slide.dragging img {
  pointer-events: none;
}

.slick-initialized .slick-slide {
  display: block;
}

.slick-loading .slick-slide {
  visibility: hidden;
}

.slick-vertical .slick-slide {
  display: block;
  height: auto;
  border: 1px solid transparent;
}

body {
  background: #FFF;
  line-height: 1.4;
}

.container {
  max-width: 1220px;
  margin: 0 auto;
  border-left: 1px solid #ede8e8;
  border-right: 1px solid #ede8e8;
}

.description {
  font-size: 14px;
  line-height: 24px;
  color: #8f8f8f;
  letter-spacing: 0.35px;
}

.subtitle {
  font-size: 42px;
  margin-bottom: 50px;
}

.subtitle-s {
  font-size: 9px;
  color: #5da8ef;
  text-transform: uppercase;
  letter-spacing: 5.4px;
}

.btn-more {
  font-size: 9px;
  letter-spacing: 5.4px;
  padding-bottom: 6px;
  border-bottom: 2px solid #5da8ef;
  text-transform: uppercase;
  background: transparent;
}

.btn-more:hover {
  border-bottom: 2px solid transparent;
}

.header {
  padding: 40px 0;
}

.header__logo {
  margin-right: 170px;
  text-decoration: none;
  vertical-align: middle;
  display: inline-block;
}

.menu {
  display: inline-block;
  vertical-align: middle;
}

.menu li {
  display: inline-block;
}

.menu a {
  font-size: 9px;
  color: #000;
  text-decoration: none;
  padding: 5px 10px;
  text-transform: uppercase;
  letter-spacing: 2px;
}

.menu a:hover {
  text-decoration: underline;
  transition: text-decoration 0.3s ease-in-out;
}

.slider-outer {
  position: realtive;
  margin-bottom: 240px;
}

.slider__item {
  position: relative;
}

.slider__item img {
  float: right;
}

.slider__text {
  text-align: left;
  position: absolute;
  top: 50%;
  transform: translate(0%, -50%);
}

.slider__text .description {
  max-width: 380px;
}

.slider__title {
  font-size: 64px;
}

.slick-btns {
  position: absolute;
  left: 0;
  bottom: 0;
}

.slick-btn {
  display: inline-block;
}

.slick-next-btn,
.slick-prev-btn {
  cursor: pointer;
  padding: 10px;
}

.slick-next-btn svg,
.slick-prev-btn svg {
  font-size: 16px;
  fill: #ebebeb;
}

.shop {
  margin-bottom: 160px;
  text-align: center;
}

.shop .subtitle {
  margin-bottom: 130px;
}

.shop__container {
  display: flex;
  justify-content: flex-start;
  margin-bottom: 40px;
}

.shop__item {
  height: 320px;
  background: #ececec;
  overflow: visible;
  width: 250px;
  position: relative;
  margin-right: 190px;
}

.shop__item img {
  position: absolute;
  left: 50%;
  transform: translate(-50%, -35%);
}

.shop__buy {
  text-align: center;
}

.shop__desc {
  position: absolute;
  top: 25%;
  left: 90%;
  text-align: left;
  width: 200px;
}

.shop__name {
  font-size: 12px;
  letter-spacing: 2.4px;
  color: #7a7a7a;
  text-transform: uppercase;
}

.shop__title {
  font-size: 28px;
  color: #333333;
  letter-spacing: 0.26px;
  margin-bottom: 20px;
}

.shop__price {
  color: #ce60a8;
  font-size: 20px;
  letter-spacing: -0.1px;
}

.shop__btn {
  background: transparent;
  text-transform: uppercase;
  font-size: 9px;
  letter-spacing: 5.4px;
  padding: 10px;
}
