
.slider-outer {
	position: realtive;
	margin-bottom: 240px; }
.slider {
	&__item {
		position: relative;
		img {
			float: right; } }
	&__text {
		text-align: left;
		position: absolute;
		top: 50%;
		transform: translate(0%,-50%);

		.description {
			max-width: 380px; } }
	&__title {
		font-size: 64px; } }

