.slick-btns {
	position: absolute;
	left: 0;
	bottom: 0; }
.slick-btn {
	display: inline-block; }

.slick-next-btn,
.slick-prev-btn {
	cursor: pointer;
	padding: 10px;
	svg {
		font-size: 16px;
		fill: #ebebeb; } }
